@charset "UTF-8";
/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */
/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";
/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import "~@ionic/angular/css/display.css";
/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
/* BonBon Buttons 1.1 by simurai.com

1.1 Added unprefixed attributes, :focus style, <button> support
1.0 Released

Usage:

	Default button: 			<a href="" class="button">Label</a>
	More fancy with icon: 		<a href="" class="button orange glossy" data-icon="â˜…">Label</a>

Following additional class names are supported:

	Color: 		orange, pink, blue, green, transparent
	Font: 		serif
	Material: 	glossy, glass
	Size: 		xs, xl
	Shape: 		round, oval, brackets, skew, back, knife, shield, drop, morph
	Icon only: 	icon
	Disabled:	disabled

*/
/* -------------- THE button -------------- */
[bonbon] {
  /* text */
  text-decoration: none;
  font: 24px/1em "Droid Sans", sans-serif;
  font-weight: bold;
  text-shadow: rgba(255, 255, 255, 0.5) 0 1px 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  /* layout */
  display: inline-block;
  position: relative;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  /* effects */
  border-top: 1px solid rgba(255, 255, 255, 0.8);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-image: -webkit-gradient(radial, 50% 0, 100, 50% 0, 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.7)));
  background-image: -moz-radial-gradient(top, ellipse cover, rgba(255, 255, 255, 0.7) 0%, rgba(255, 255, 255, 0) 100%);
  background-image: gradient(radial, 50% 0, 100, 50% 0, 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.7)));
  -webkit-transition: background 0.2s ease-in-out;
  -moz-transition: background 0.2s ease-in-out;
  transition: background 0.2s ease-in-out;
  /* color */
  color: #666666 !important;
  background-color: #bfbfbf;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #999999 0 0.1em 3px, #737373 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #999999 0 0.1em 3px, #737373 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #999999 0 0.1em 3px, #737373 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
}

[bonbon]:not(img) {
  padding: 0.5em 0.6em 0.4em 0.6em;
  margin: 0.5em;
}

/* -------------- icon -------------- */
[bonbon]:before {
  font: 1.2em/0 sans-serif;
  /* gets the icon value from the custom data attribute and puts it infront of the button label */
  content: attr(data-icon);
  margin-right: 6px;
}

/* icon only */
.icon {
  font-weight: normal;
  font-style: normal;
}

.icon:before {
  display: block;
  height: 0.75em;
  line-height: 0.75em;
  margin: 0;
}

/* -------------- colours -------------- */
[bonbon].orange {
  color: #996300 !important;
  background-color: #ffa600;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #cc8500 0 0.1em 3px, #996300 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #cc8500 0 0.1em 3px, #996300 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #cc8500 0 0.1em 3px, #996300 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
}

[bonbon].orange:hover {
  background-color: #ffc14d;
}

[bonbon].blue {
  color: #336999 !important;
  background-color: #80c4ff;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #5390c6 0 0.1em 3px, #336999 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #5390c6 0 0.1em 3px, #336999 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #5390c6 0 0.1em 3px, #336999 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
}

[bonbon].blue:hover {
  background-color: #a8d7ff;
}

[bonbon].green {
  color: #508217 !important;
  background-color: #9ee052;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #6bad1f 0 0.1em 3px, #508217 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #6bad1f 0 0.1em 3px, #508217 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #6bad1f 0 0.1em 3px, #508217 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
}

[bonbon].green:hover {
  background-color: #c2ec93;
}

[bonbon].pink {
  color: #990033 !important;
  background-color: #ff80aa;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #d92662 0 0.1em 3px, #b8144b 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #d92662 0 0.1em 3px, #b8144b 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
  box-shadow: inset rgba(255, 254, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.15) 0 -0.1em 0.3em, #d92662 0 0.1em 3px, #b8144b 0 0.3em 1px, rgba(0, 0, 0, 0.2) 0 0.5em 5px;
}

[bonbon].pink:hover {
  background-color: #ffa8c5;
}

[bonbon].transparent {
  color: rgba(0, 0, 0, 0.5) !important;
}

[bonbon].transparent,
[bonbon].transparent:hover,
[bonbon].transparent:active {
  background-color: transparent;
  background-image: none;
}

[bonbon].transparent:hover {
  opacity: 0.9;
}

/* -------------- States -------------- */
[bonbon]:hover {
  background-color: #d4d4d4;
}

[bonbon]:active {
  background-image: -webkit-gradient(radial, 50% 0, 100, 50% 0, 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0)));
  background-image: -moz-gradient(radial, 50% 0, 100, 50% 0, 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0)));
  background-image: gradient(radial, 50% 0, 100, 50% 0, 0, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0)));
  -webkit-box-shadow: inset rgba(255, 255, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.2) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.4) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  -moz-box-shadow: inset rgba(255, 255, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.2) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.4) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  box-shadow: inset rgba(255, 255, 255, 0.6) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.2) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.4) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  -webkit-transform: translateY(0.2em);
  -moz-transform: translateY(0.2em);
  transform: translateY(0.2em);
}

[bonbon][disabled],
[bonbon][disabled]:hover,
[bonbon].disabled,
[bonbon].disabled:hover {
  opacity: 0.5;
  cursor: default;
  color: rgba(0, 0, 0, 0.2) !important;
  text-shadow: none !important;
  background-color: rgba(0, 0, 0, 0.05);
  background-image: none;
  border-top: none;
  -webkit-box-shadow: inset rgba(255, 254, 255, 0.4) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.1) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.3) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  -moz-box-shadow: inset rgba(255, 254, 255, 0.4) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.1) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.3) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  box-shadow: inset rgba(255, 254, 255, 0.4) 0 0.3em 0.3em, inset rgba(0, 0, 0, 0.1) 0 -0.1em 0.3em, rgba(0, 0, 0, 0.3) 0 0.1em 1px, rgba(0, 0, 0, 0.2) 0 0.2em 6px;
  -webkit-transform: translateY(5px);
  -moz-transform: translateY(5px);
  transform: translateY(5px);
}

/* -------------- Fonts -------------- */
.serif {
  font-family: "Lobster", serif;
  font-weight: normal;
}

/* -------------- Sizes -------------- */
.xs {
  font-size: 16px;
}

.xl {
  font-size: 32px;
}

/* -------------- Materials -------------- */
[bonbon].glossy:after,
[bonbon].glass:after {
  content: "";
  position: absolute;
  width: 90%;
  height: 60%;
  top: 0;
  left: 5%;
  -webkit-border-radius: 0.5em 0.5em 1em 1em/0.5em 0.5em 2em 2em;
  -moz-border-radius: 0.5em 0.5em 1em 1em/0.5em 0.5em 2em 2em;
  border-radius: 0.5em 0.5em 1em 1em/0.5em 0.5em 2em 2em;
  background-image: -webkit-gradient(linear, 0% 0, 100% 0, from(rgba(255, 255, 255, 0.55)), to(rgba(255, 255, 255, 0.5)), color-stop(0.5, rgba(255, 255, 255, 0)), color-stop(0.8, rgba(255, 255, 255, 0)));
  background-image: -moz-linear-gradient(left, rgba(255, 255, 255, 0.55), rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0) 80%, rgba(255, 255, 255, 0.5));
  background-image: gradient(linear, 0% 0, 100% 0, from(rgba(255, 255, 255, 0.55)), to(rgba(255, 255, 255, 0.5)), color-stop(0.5, rgba(255, 255, 255, 0)), color-stop(0.8, rgba(255, 255, 255, 0)));
}

[bonbon].glossy:active:after,
[bonbon].glass:active:after,
[bonbon].disabled:after,
[bonbon][disabled]:after {
  opacity: 0.6;
}

[bonbon].icon.glossy:after,
[bonbon].icon.glass:after {
  height: 75%;
}

/* -------------- Glass + Transparent -------------- */
[bonbon].glass {
  text-shadow: rgba(255, 255, 255, 0.5) 0 -1px 0, rgba(0, 0, 0, 0.18) 0 0.18em 0.15em;
}

[bonbon].glass:active {
  text-shadow: rgba(255, 255, 255, 0.3) 0 1px 0, rgba(0, 0, 0, 0.15) 0 0.18em 0.15em;
}

/* -------------- Shapes -------------- */
/* round */
.round,
.round.glossy:after,
.round.glass:after {
  border-top: none;
  -webkit-border-radius: 1em;
  -moz-border-radius: 1em;
  border-radius: 1em;
}

/* oval */
.oval {
  border-top: none;
  padding-left: 0.8em;
  padding-right: 0.8em;
  -webkit-border-radius: 5em/2em;
  -moz-border-radius: 5em/2em;
  border-radius: 5em/2em;
}

.oval.glossy:after,
.oval.glass:after {
  top: 5%;
  -webkit-border-radius: 5em/2em 2em 1em 1em;
  -moz-border-radius: 5em/2em 2em 1em 1em;
  border-radius: 5em/2em 2em 1em 1em;
}

.oval.icon {
  padding-left: 0.8em;
  padding-right: 0.8em;
  -webkit-border-radius: 1.5em/1em;
  -moz-border-radius: 1.5em/1em;
  border-radius: 1.5em/1em;
}

.oval.icon.glossy:after,
.oval.icon.glass:after {
  -webkit-border-radius: 1.5em/1em;
  -moz-border-radius: 1.5em/1em;
  border-radius: 1.5em/1em;
}

/* brackets */
.brackets,
.brackets.glossy:after,
.brackets.glass:after {
  border-top: none;
  -webkit-border-radius: 0.5em/1em;
  -moz-border-radius: 0.5em/1em;
  border-radius: 0.5em/1em;
}

/* skew */
.skew {
  border-top: none;
  padding-right: 1.2em;
  padding-left: 0.8em;
  -webkit-border-radius: 5em 1em/5em 1em;
  -moz-border-radius: 5em 1em/5em 1em;
  border-radius: 5em 1em/5em 1em;
}

.skew.glossy:after,
.skew.glass:after {
  left: 10%;
  -webkit-border-radius: 7em 1em/5em 1em;
  -moz-border-radius: 7em 1em/5em 1em;
  border-radius: 7em 1em/5em 1em;
}

.skew.icon {
  padding-right: 0.9em;
  padding-left: 0.8em;
}

/* back */
.back,
.back.glossy:after,
.back.glass:after {
  border-top-color: rgba(255, 255, 255, 0.5);
  -webkit-border-radius: 1.6em 1.6em 1em 1em/4em 4em 1em 1em;
  -moz-border-radius: 1.6em 1.6em 1em 1em/4em 4em 1em 1em;
  border-radius: 1.6em 1.6em 1em 1em/4em 4em 1em 1em;
}

.back.glossy:after,
.back.glass:after {
  left: 6%;
  width: 88%;
}

/* knife */
.knife {
  padding-left: 1.5em;
  -webkit-border-radius: 0.2em 0.5em 0.5em 8em/0.2em 0.5em 0.5em 5em;
  -moz-border-radius: 0.2em 0.5em 0.5em 8em/0.2em 0.5em 0.5em 5em;
  border-radius: 0.2em 0.5em 0.5em 8em/0.2em 0.5em 0.5em 5em;
}

.knife.glossy:after,
.knife.glass:after {
  left: 3%;
  width: 97%;
  -webkit-border-radius: 0.1em 0.5em 0.5em 8em/0.1em 0.5em 0.5em 2em;
  -moz-border-radius: 0.1em 0.5em 0.5em 8em/0.1em 0.5em 0.5em 2em;
  border-radius: 0.1em 0.5em 0.5em 8em/0.1em 0.5em 0.5em 2em;
}

.knife.glossy.icon:after,
.knife.glass.icon:after {
  left: 5%;
  width: 95%;
  -webkit-border-radius: 0.5em 0.5em 1em 6em/0.5em 0.5em 1em 4em;
  -moz-border-radius: 0.5em 0.5em 1em 6em/0.5em 0.5em 1em 4em;
  border-radius: 0.5em 0.5em 1em 6em/0.5em 0.5em 1em 4em;
}

/* shield */
.shield,
.shield.glossy:after,
.shield.glass:after {
  -webkit-border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
  -moz-border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
  border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
}

.shield {
  padding-left: 0.8em;
  padding-right: 0.8em;
}

.shield.icon {
  padding-left: 0.6em;
  padding-right: 0.6em;
}

/* drop */
.drop {
  border-top: none;
  -webkit-border-radius: 2em 5em 2em 0.6em/2em 4em 2em 0.6em;
  -moz-border-radius: 2em 5em 2em 0.6em/2em 4em 2em 0.6em;
  border-radius: 2em 5em 2em 0.6em/2em 4em 2em 0.6em;
}

.drop.glossy:after,
.drop.glass:after {
  left: 4%;
  -webkit-border-radius: 2em 6em 2em 1em/2em 4em 2em 2em;
  -moz-border-radius: 2em 6em 2em 1em/2em 4em 2em 2em;
  border-radius: 2em 6em 2em 1em/2em 4em 2em 2em;
}

.drop.icon {
  padding-right: 0.6em;
}

/* morph */
.morph {
  border-top: none;
  -webkit-border-radius: 5em/2em;
  -moz-border-radius: 5em/2em;
  border-radius: 5em/2em;
  -webkit-transition: -webkit-border-radius 0.3s ease-in-out;
  -moz-transition: -moz-border-radius 0.3s ease-in-out;
  transition: -moz-border-radius 0.3s ease-in-out;
}

.morph:hover {
  -webkit-border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
  -moz-border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
  border-radius: 0.4em 0.4em 2em 2em/0.4em 0.4em 3em 3em;
}

.morph:active {
  -webkit-border-radius: 0.3em;
  -moz-border-radius: 0.3em;
  border-radius: 0.3em;
}

.morph:after {
  display: none;
}

